.experience{background-color:#ffd9c0;display:flex;flex-direction:column;align-items:center}.experience .container::-webkit-scrollbar{display:none;width:90%;display:flex;flex-wrap:wrap}.experience h1{text-align:left;font-size:50px;text-decoration:underline}@media(max-width: 768px){.experience h1{font-size:20px}}.experience ul{margin:10px;padding:0;list-style:none;display:flex}@media(max-width: 768px){.experience ul{margin:10px 0;flex-wrap:wrap;justify-content:center}}@media(max-width: 768px){.experience .container{width:100%}}.experience .container .tittle{margin-top:10px;font-size:30px;font-weight:bold;margin-left:10%;width:80%}.experience .container .subtitle{margin-bottom:5px;font-size:20px;font-style:italic;font-weight:lighter;width:80%}.experience .container .body{font-weight:lighter;font-size:18px;padding-left:1.5%;width:90%;margin-bottom:2.5%}.experience .container .myList{content:"■";font-size:1.1em}.experience .container .myList .container::-webkit-scrollbar{display:none;width:90%;display:flex;flex-wrap:wrap}